/* -------------------------------
   Layout general del profile
--------------------------------*/
.profile-page {
  display: flex;
  margin: 0;
  font-family: Arial, sans-serif;
}

/* -------------------------------
   Menú lateral fijo
--------------------------------*/
.sidebar {
  position: fixed;
  top: 0;
  left: 0;
  width: 220px;
  height: 100vh;
  background-color: #1f2937;
  color: white;
  display: flex;
  flex-direction: column;
  padding: 0;
  z-index: 500;
  padding-top: 0;
}

.sidebar ul {
  list-style: none;
  padding: 0;
  margin: 0;
  width: 100%;
}

.sidebar li {
  padding: 15px 20px;
  cursor: pointer;
  transition: background 0.2s;
  font-weight: bold;
  font-size: 15px;
  border-bottom: 1px solid #00000033;
  align-items: center;
  justify-content: flex-start;
}

.sidebar li:hover {
  background-color: #000000;
}

.sidebar li.active {
  background-color: #32cd32;
  color: white;
}

.sidebar li form button {
  all: unset; /* Quita todos los estilos por defecto del botón */
  display: block;
  width: 100%;
  font-weight: bold;
  font-size: 15px;
  cursor: pointer;
  color: white;
  background: none;
  text-align: left;
}

.sidebar li form button:hover {
  all: unset;
  background-color: #000000;
}

.sidebar li form button:active {
  background-color: #32cd32; /* igual que active si quieres */
}

/* -------------------------------
   Contenido principal
--------------------------------*/
.content {
  margin-left: 220px;
  padding: 20px;
  min-height: calc(100vh - 60px);
  overflow-y: auto;
  width: calc(100% - 220px); /* ocupa todo el ancho restante */
  box-sizing: border-box;
}

/* -------------------------------
   Perfil de usuario
--------------------------------*/
.user-profile {
  max-width: 420px;
  background: white;
  border-radius: 20px;
  padding: 30px 35px;
  box-shadow: 0 4px 14px rgba(0, 0, 0, 0.15);
  margin: 0 auto;
}

/* Campos de usuario */
.field {
  display: flex;
  flex-direction: column;
  margin-bottom: 12px;
}

.field label {
  font-size: 14px;
  color: #555;
  margin-bottom: 5px;
  display: flex;
  align-items: center;
  gap: 5px;
}

.field input {
  width: 100%;
  padding: 10px;
  border-radius: 10px;
  border: 1px solid #999;
}

.field span {
  padding: 10px;
  background: #f7f7f7;
  border-radius: 10px;
  border: 1px solid #ddd;
}

/* Botones */
.buttons {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-top: 10px;
  border-radius: 8px;
  font-weight: bold;
  color: white;
  cursor: pointer;
  transition: background 0.2s;
}

.btn-edit { background: #1f2937; color: white; }
.btn-edit:hover { background: #000; }

.btn-save { background: #32cd32; color: white; }
.btn-cancel { background: #e63946; color: white; }

/* Botones de perfil con estilo de botones de direcciones, pero más grandes */
.profile-buttons .btn-edit,
.profile-buttons .btn-save,
.profile-buttons .btn-cancel {
  padding: 12px 20px; /* un poco más grandes */
  border-radius: 8px;
  font-size: 16px;
  font-weight: bold;
  color: white;
  cursor: pointer;
  transition: background 0.2s;
}

.profile-buttons .btn-edit { background: #1f2937; }
.profile-buttons .btn-edit:hover { background: #000; }

.profile-buttons .btn-save { background: #0b3d0b; }
.profile-buttons .btn-save:hover { background: #062206; }

.profile-buttons .btn-cancel { background: #8b0000; }
.profile-buttons .btn-cancel:hover { background: #4d0000; }

/* Separación entre botones */
.profile-buttons {
  display: flex;
  gap: 10px;
  margin-top: 20px;
  justify-content: center;
  flex-wrap: wrap;
}

/* -------------------------------
   Direcciones
--------------------------------*/
.address-form-container {
  max-width: 800px;
  margin: 0 auto;
}

h2 { color: #1f2937; margin-top: 30px; margin-bottom: 15px; }

/* Vista compacta */
.address-compact {
  border: 1px solid #ccc;
  padding: 10px 15px;
  margin-bottom: 10px;
  border-radius: 8px;
  background-color: #f4f4f4;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 15px;
}

.address-compact.selected {
  background-color: #c0e0e5;
  border-color: #006064;
  color: #004d4d;
}

/* Formulario desplegable */
.address-form {
  border: 1px solid #999;
  padding: 15px;
  margin-bottom: 10px;
  border-radius: 8px;
  background-color: #eaeaea;
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.address-form input {
  padding: 8px 10px;
  border-radius: 8px;
  border: 1px solid #999;
  font-size: 14px;
  flex: 1 1 200px;
  max-width: 48%;
  box-sizing: border-box;
}

.address-buttons {
  display: flex;
  gap: 10px;
  margin-top: 10px;
  flex-basis: 100%;
}

/* Botones */
.default-btn { background: #1f2937; color: white; }
.default-btn:hover { background: #000; }

.save-btn { background: #0b3d0b; color: white; }
.save-btn:hover { background: #062206; }

.cancel-btn { background: #8b0000; color: white; }
.cancel-btn:hover { background: #4d0000; }

.delete-btn { background: #bf360c; color: white; }
.delete-btn:hover { background: #8e2e0b; }

.add-btn { background: #1f2937; color: white; }
.add-btn:hover { background: #000; }

/* -------------------------------
   Responsive
--------------------------------*/
@media (max-width: 768px) {
  .sidebar {
    position: fixed; /* que siga visible si quieres que sea sticky */
    top: 0;
    left: 0;
    width: 100%;
    height: auto; /* se adapta al contenido */
    flex-direction: row;
    overflow-x: auto; /* scroll horizontal si hay muchas opciones */
    background-color: #1f2937;
    z-index: 500;
  }

  .sidebar ul {
    display: flex;
    padding: 0;
    margin: 0;
    width: max-content; /* se expande según el contenido */
  }

  .sidebar li {
    flex: none; /* no se estiren */
    text-align: center;
    border-bottom: none;
    border-right: 1px solid #00000033;
  }

  .sidebar li:last-child { border-right: none; }

  .content {
    margin-left: 0; /* quitar margen lateral */
    width: 100%; /* ocupar todo el ancho */
    padding: 15px; /* padding interno */
  }

  .profile-buttons {
    flex-direction: column;
    gap: 10px;
  }

  .profile-buttons .btn-edit,
  .profile-buttons .btn-save,
  .profile-buttons .btn-cancel {
    width: 100%; /* botones full width */
    font-size: 15px; /* ajustar un poco */
  }
}
